.pl{
    display: flex;
    padding: 50px 100px;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.pl-desc{
    width: 70%;
    margin: 20px 0px;
}
.pl-title{
    font-size: 30px;
    font-weight: 400;
}
.pl-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap-reverse;
    align-items: center;
    justify-content: center;
}